import React, { Component, Fragment, useState } from 'react'
import methods from '@utils/method'
// import VConsole from 'vconsole'

if (methods.isMiniprogram) {
  wx.setEnableDebug({
    enableDebug: true
  });
} //else {
//   new VConsole();
// }


import './index.scss'

export default function Wraper(props){
    let { title = '', loadend, children, classN='' } = props
    let [isCallback, setIsCallback] = useState(true)
    
    // console.log('prop s', props)
    
    // 设置页面标题
    if (methods.isMiniprogram) {
      wx.setNavigationBarTitle({
        title,
      })
      // wx.showToast({
      //   title,
      // })
    } else {
      document.title = title;
    }

    // 加载完成回调函数，如果调用了一次，下次render则不再调用
    if (isCallback && Object.prototype.toString.call(loadend) === '[object Function]') {
        setIsCallback(false)
        loadend();
    }

  return <div className={`wraper ${classN}`}>{children}</div>
}