import React, { Component, Fragment, useState } from 'react'
import ReactDOM from 'react-dom'
import axios from 'axios'
import mpAdapter from 'axios-miniprogram-adapter'
import {Button, Toast} from 'antd-mobile'

import methods from '@utils/method'

import Wraper from '@components/wraper'
import FixedTool from '@components/FixedTool'

import '@style/common.scss'
import '@style/detail.scss'

// console.log('process.env.isMiniprogram', process.env.isMiniprogram)
if (methods.isMiniprogram) {
  axios.defaults.adapter = mpAdapter
}
function setData() {
  // let [list, setList] = useState([]);
  // console.log('list', list, setList);
  let gameData = {
    title: '',
    // textareaValue: '',
    list: [
        
    ]
}
  return gameData
  // setList(list);
}
class Detail extends Component {
  constructor(props) {
    super(props)
    this.state = {
      // inputTxt: '',
      title: '详情标题',
      // textareaValue: '',
      // 是否删除重复
      isCheckDuplicate: true,
      list: [[]]
    }
  }
  componentDidMount() {
    methods.getPageQuery(query => this.fnPageInit(query))
  }

  fnPageInit(query) {
    let gameData = setData();
    this.setState({
      title: decodeURIComponent(query.name),
      gameData,
    })
  }

  clickHandleCreateGame() {
    let { isCheckDuplicate } = this.state;

    let inputTxt = this.areaTxt.value || '';   // 小程序中 this.areaTxt.value 可能会是undefined，因此需要默认值

    // 字符串去除空格
    inputTxt = inputTxt.replace(/\s/g, '');
    if (inputTxt.length <= 0) {
      Toast.info('请输入字符再生成');
      return;
    }

    // 字符串打散成数组
    let arr = inputTxt.split('');

    // 数组去重
    if (isCheckDuplicate) {
      arr = [...new Set(arr)]
    }

    // 分页
    let newArr = []
    let pageArr = []
    arr.forEach((item, i) => {
      if (i % 20 === 0) {
        // console.log('pageArr1', pageArr)
        if(i!==0){
          newArr.push(pageArr);
        }
        pageArr = [];
      }
      // console.log('item', item)
      pageArr.push(item);
      if (arr[i+1] === undefined) {
        newArr.push(pageArr);
        pageArr = [];
      }
    })
    // console.log('newArr', newArr)
    inputTxt.length && this.setState({
      list: newArr,
    });
  }

  clickCheckDuplicate() {
    let { isCheckDuplicate } = this.state;
    // console.log('isCheckDuplicate', isCheckDuplicate)
    this.setState({
      isCheckDuplicate: !isCheckDuplicate,
    })
  }

  hideForm() {
    this.setState({
      isShowForm: false,
    })
  }

  render() {
    let { list = [[]], title, isCheckDuplicate, isShowForm=true } = this.state;
    // console.log('loist', list)
    return (
      <Wraper title={title} classN="page_detail">
        <div className="input_box print">
          {/* <div>在输入框内输入想要学习在字，然后点击生成纸牌，打印出来就可以和开始玩游戏了。</div> */}
          <div><textarea className="textarea" v-show={isShowForm} ref={(el) => this.areaTxt = el} placeholder="输入至少两个字然后点击生成按钮"></textarea></div>
          <div className="mb10"><div className="label"><input className="checkbox" type="checkbox" checked={isCheckDuplicate} onChange={() => this.clickCheckDuplicate()} /><span className='tip'>删除重复的字再生成</span></div></div>
          
          <Button size="small" type="primary" inline onClick={() => this.clickHandleCreateGame()}>生成游戏纸牌</Button>

          {/* <div className="rules">规则</div> */}
        </div>
        
        <FixedTool className="print" hideForm={()=>this.hideForm()} />

        
        
        
        {list && list.length > 0 ? (
          <div>
            {list.map((li, i) => {
              return (li && li.length>0 && <ul className="card_list" key={i}>
                {li.map((item, subi) => {
                  // console.log(li,subi,li[subi + 1])
                  let nextWord = li[subi + 1] || (list[i + 1] && list[i + 1][0]);
                  return (nextWord!==undefined && <li key={subi} className="card_item">
                    <div className="card_word">
                      <div>我有</div>
                      <div className="big">{item}</div>
                      <div>谁有</div>
                      <div className="middle">{nextWord}</div>
                    </div>
                  </li>)})}
                </ul>)
            })}
          </div>
        ) : (
          null
        )}
      </Wraper>
    )
  }
}

// export default Detail

export default function createApp() {
  const container = document.createElement('div')
  container.id = 'app'
  document.body.appendChild(container)

  ReactDOM.render(<Detail />, container)
}

;('undefined' != typeof wx && wx.getSystemInfoSync) || createApp()
